import { Component, OnInit,
trigger, state, style, transition, animate, keyframes } from '@angular/core';
import { BlockService } from './../block.service';
import { Router } from '@angular/router';
var babbler = require('babbler');


@Component({
  templateUrl: './contentarea.component.html',
  styleUrls: ['./contentarea.component.css'],
  animations:[
    trigger('focusPanel',[
      state('inactive', style({
        transform: 'scale(1)',
        backgroundColor: '#fff'
      })

      ),
      state('active', style({
        transform: 'scale(1.2)',
        backgroundColor: '#ddaaaa'

      })),
      transition('active => inactive', animate('200ms ease-in')
      ),

      transition('inactive => active', animate('200ms ease-in')
      )
    ]),
    trigger('movePanel', [
      /*transition('void => *', [
        animate(600, keyframes([
          style({opacity: 0, transform: 'translateX(-200px)', offset: 0}),
          style({opacity: 1, transform: 'translateX(20px)', offset: 0.75}),
          style({opacity: 1, transform: 'translateX(0px)', offset: 1})
        ]))
      ])*/
    ])
  ]
})
export class ContentareaComponent implements OnInit {

  blockObjects = [];
  activeBlock;
  activeId;
  private state: string = 'inactive';
  constructor(
    private blockService:BlockService,
    private router: Router
  ) {
  }

  ngOnInit() {
      this.loadBlocksFromService();
  }

  loadBlocksFromService(){

  console.log("it is in the load blocks from service");
    this.blockService.loadBlocks().subscribe(data => {
    console.log("-------------------------------------------------");
    console.log("data returned from block", data);
    let carouselstatus = 'active';
            data.forEach( blockData => {
                console.log("block got is ", blockData);
                blockData.carouselstatus = carouselstatus;
                this.blockObjects.push(blockData);
                console.log("-------------------------------------------------");
                console.log("data returned from block", this.blockObjects);
                carouselstatus = '';
            });

            if(this.blockObjects.length > 0)
            {
              this.activeBlock = this.blockObjects[0];

            }
    });
  }

  showRelatedBlocks(block)
  {
      if(block.associatedTo.length > 0)
      {
        this.activeBlock = block;
        this.state = 'inactive';
      }
  }

  makespeech(block) {

    //state = 'active';
    this.activeId = block._id;

    this.state = 'active';


    console.log("clicked on make speech function", block);

    babbler.speak(block.objname).then(
      e =>{
        this.state = 'inactive';
       console.log('speak text finished', e);
     });
    console.log('speek is passed')
  }





}



